.container2 {
  font-family: "Inter";
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  /* overflow-y: hidden; */
  display: flex;
  justify-content: flex-start;
}
.navbar2 {
  display: inline-block;
  width: 18%;
  /* box-shadow: 3px 0 8px 2px #f1f0f0; */
  height: 100%;
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
}
.nav__list {
  display: inline-block;
  margin: 0;
  text-align: center;
  width: 100%;
  padding: 0;
  list-style-type: none;
}
.nav__link:link,
.nav__link:visited {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
}
.section__main {
  display: inline-block;
  width: 100%;
  /* margin-left: 18%; */
}
.section__header {
  background-color: rgb(253, 250, 250);
  margin-left: 1px;
  width: 100%;
  padding: 20px;
  box-shadow: 3px 0 8px 2px #f1f0f0;
  display: flex;
  align-items: center;
  z-index: 2;
  width: inherit;
  position: fixed;
  top: 0;
}
/* .section__header--link { */
/* margin-left: 80px; */
/* } */

.section__content {
  width: 979px;
  display: flex;
  justify-content: flex-start;
  font-family: "Inter";
  font-weight: 400;
  z-index: 1;
  position: relative;
  margin: 80px auto 20px;
  /* margin-left: 120px; */
}
.section__description {
  width: 95%;
  margin: auto;
  display: inline-block;
  position: relative;
}
.design__change {
  width: 929px;
  padding-left: 20px;
  margin: 0 auto 80px;
}
.section__tags {
  border: 1px solid #d6dae0;
  padding: 20px 6px 20px 10px;
  border-radius: 5px;
  display: inline-block;
  flex-direction: column;
  margin-left: 30px;
  width: 30%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  /* display: flex; */
  /* align-items: center/; */
  height: fit-content;
  /* height: 100px; */
}

.section__tagtitle {
  display: inline-block;
  font-size: calc(0.78125rem + 6px);
  font-weight: 500;
  letter-spacing: -0.03em;
  line-height: 14px;
  margin: 0 10px 12px;
}

.section__tags--wrapper {
  display: flex;
  width: 100%;
  height: auto;
  flex-wrap: wrap;
}

.section__tags--button {
  display: inline-block;
  margin: 8px 10px;
  font-family: "Inter";
  background: #eff2f6;
  outline: none;
  border: 1px solid #d6dae0;
  border-radius: 5px;
  align-items: flex-start;
  height: 40px;
  padding: 5px;
  font-size: 14px;
}

.heading__wrapper {
  display: flex;
  justify-content: space-between;
}
.content__heading {
  display: inline-block;
  font-family: "Inter";
  font-weight: 600;
  font-size: 20px;
  margin: 10px 10px 6px 10px;
}
.content__remote {
  background-color: #e1faf3;
  color: #005a37;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 0 5px;
}
.content__para {
  margin: 10px;
  width: 100%;
  line-height: 1.4;
  font-size: 16px;
  padding-top: 20px;
}
.content__img {
  grid-area: logo;
  border: 1px solid #b1b1b1;
  border-radius: 10px;
  /* padding: 0.5rem; */
  height: 75px;
  width: 75px;
}

.content__container {
  width: 70%;
  float: left;
  padding: 15px;
}

.content__company__wrapper {
  width: 60%;
  float: left;
  padding-top: 20px;
}

.content__company {
  /* margin: 15px; */
  margin: 5px;
  display: flex;
  padding: 20px;
  border-radius: 20px;
}
.company__title {
  display: inline-block;

  font-family: Inter;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.02em;
  font-weight: 600;
  /* margin: 10px 10px 0px 10px; */
  margin: 1px 10px 5px 10px;
  transform: translateY(-10%);
}
.company__desc {
  margin-left: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.company__loc {
  display: inline-block;
  font-size: calc(0.78125rem + 6px);
  font-weight: 500;
  letter-spacing: -0.03em;
  line-height: 14px;
  margin: 0px 10px 12px 10px;
}
.company__para {
  font-size: calc(0.71875rem + 5px);
  line-height: 14px;
  letter-spacing: -0.03em;
  font-weight: 400;
  margin: 0px 10px 12px 10px;
}
.company__para-1 {
  font-size: calc(0.71875rem + 5px);
  line-height: 14px;
  letter-spacing: -0.03em;
  font-weight: 400;
  margin: 0px 10px 12px 10px;
  /* margin-bottom: 0.75em; */
}
.popup {
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.459);
  position: absolute;
  min-height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;
  left: 0;
}
.popup__wrap {
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  width: 40%;
  padding: 0px;
}
.popup__content {
  /* max-width: 50ch;
    display: flex; */
  padding: 20px 30px 20px 30px;

  /* flex-direction: column;
    justify-content: Start; */
}
.popup__warn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup__img {
  width: 90px;
  /* width: 72px; */
  /* margin: 10px 15px 0 0; */
}
.popup__button {
  display: flex;
  align-self: end;
  justify-content: center;
  width: 20%;
  font-family: "Inter";
  background-color: #516cf0;
  color: rgb(255, 255, 255);
  border: 0px none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  padding: 10px;
}
.profile__button {
  padding: 15px;
  background-color: #516cf0;
  color: rgb(255, 255, 255);
  border: 0px none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
}
.is-hidden {
  display: none;
}

.card-image {
  padding: 0;
  border: 0;
}

