.jobOpenings__card {
  display: flex;

  flex-direction: column;

  overflow: hidden;
  border-radius: 0.4525rem;
  border: 1px solid #d6dae0;
  background: #fff;

  /* height: 180px; */
  height: 160px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  transition: box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1);

  overflow: hidden;
  border-radius: 0.3125rem;
  border: 1px solid #d6dae0;
  background: #fff;
  transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
}

.jobOpenings__card:hover {
  box-shadow: 0px 3px 3px 1px rgba(0, 0, 0, 0.1);
}

.card__section {
  display: grid;
  grid-gap: 1rem;
  margin-bottom: 30px;
