.research__navbar {
    background-color: #fff;
    height: 80px;
}

.research__container {
    background-color: #edf3f3;
    padding: 0.9375rem;
}

.research__cards {
    width: 95%;
    padding: 0.9375rem;
    margin: auto;
    border-radius: 0.3125rem;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border: 1px solid #d6dae0;
    background: #fff;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    box-sizing: border-box;
    grid-gap: 15px 12px;
}

.research__card {
    height: 225px;
    width: auto;
    max-width: 350px;
    overflow: hidden;
    border-radius: 0.4525rem;
    border: 1px solid #d6dae0;
    background: #fff;
    cursor: pointer;
    transition: all 0.2s ease-in;
}

.research__card:hover {
  box-shadow: 0px 3px 3px 1px rgba(0, 0, 0, 0.1);
}

.research__card > img {
    height: 125px;
    background-size: contain;
    width: 100%;
    margin-top: -2px;
}

.research__card > p {
    font-size: 1rem;
    line-height: 1.375rem;
    font-weight: 500;
    margin-top: 0.9375rem;
    margin-bottom: 0.9375rem;
    padding: 0px 0.9375rem;
}

.research__btn {
    border: none;
    border: 1px solid #d6dae0;
    border-top: none;
    border-color: #d6dae0;
    margin-left: 2.5%;
    height: 56px;
    width: 95%;
    outline: none;
    background-color: #fff;
    cursor: pointer;
    color: #201e27;
    padding: 0.375rem 0.75rem;
    border-bottom-left-radius: 0.3125rem;
    border-bottom-right-radius: 0.3125rem;
    font-weight: 600;
    font-size: 0.875rem;
    height: 2.5rem;
    transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
