.research__popup {
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.259);
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 110%;
    float: left;
    left: 0;
    top: 0;
