.inputSearch > input, .prefSearch > input {
    font-size: .875rem;
    margin: 0;
    margin-bottom: 13px;
    background: transparent;
    color: #030303;
    caret-color: #2dc5a1;
    display: block;
    height: 2.5rem;
    line-height: 1.375rem;
    outline: none;
    padding: 0 .875rem .1em;
    width: 94.5%;
    border-radius: .3125rem;
    border: 1px solid #ddd;
    transition: all 0.2s ease-in;
}

.inputSearch > input:focus, .prefSearch > input:focus {
    border: 1px solid #000;
}

.inputSearch > ul {
    position: absolute;
    z-index: 1;
    width: 89%;
    border: 1px solid #ddd;
    margin-top: 0px;
    padding: 10px 0px;
    list-style: none;
    border-radius: 10px;
    background-color: #fff;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 140px;
}

.inputSearch > ul > li {
    display: flex;
    font-size: .875rem;
    font-weight: 600;
    cursor: pointer;
    padding: 5px 10px;
    align-items: center;
    margin-bottom: 5px;
    transition: all 0.2s ease-in;
}

.inputSearch > ul > li:hover {
    background-color: #f5f5f5;
}

.inputSearch > ul > li:last-child {
    margin-bottom: 0px;
}

.inputSearch > ul > li > img {
    margin-right: 10px;
}

/* Pref Search */
.prefSearch {
    position: relative;
}

.prefSearch > input {
    margin-left: .5rem;
    width: 90%;
    margin-top: -7px;
    margin-bottom: 7px;
}

.prefSearch > div {
    position: absolute;
    overflow: hidden;
    cursor: pointer;
    z-index: .5;
    font-size: .875rem;
    font-weight: 600;
    width: 95.25%;
    margin-left: .6rem;
    background-color: #fff;
    border: 1px solid #ddd;
    padding: .5rem 0;
    border-radius: 5px;
}

.prefSearch > div > span { 
    padding: 10px;
    width: 100%;
    display: block;
    transition: all 0.2s ease-in;
}

.prefSearch > div > span:hover {
    background-color: #f5f5f5;
}

