}

input, button, textarea, select, option {
    font-family: "Inter", sans-serif !important;
}

.btn {
    outline: none;
    border: none;
    padding: 10px 15px;
    font-weight: 600;
    background-color: #01a3a4;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
}

