.sidebar__qn {
  text-align: center;
  display: flex;

  cursor: pointer;
  font-size: 18px;
  line-height: 1.5;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  font-family: Inter, sans-serif;

  /* border-radius: 3px; */
  text-transform: none;
  margin-right: 10px;
}

.studentSidebarQuestion {
  display: flex;
  align-items: center;
  color: #7f7f83;
  cursor: pointer;
  padding-left: 20px;
  font-family: Inter, sans-serif;
  justify-content: center;
  width: 90%;
  margin: auto;
  height: 100px;
  border-radius: 6px;
  margin-bottom: 10px;
}

.studentSidebarQuestion:active {
  border-left: 4px solid #00a0fc;
  /* margin: auto 0; */
  border-radius: 2px;
}

.studentSidebarQuestion > p {
  display: none;
  font-size: 11px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 250px;
  padding-right: 5px;
  line-height: 1.5em;
  height: 3em;
}

.indexing {
  top: 180px;
  right: 0;
  width: 100px;
  bottom: 20px;
  display: flex;
  padding: 14px 5px 17px 9px;
  z-index: 200;
  position: fixed;
  overflow-x: hidden;
  text-align: left;
  transition: width 0.3s;
  align-items: flex-end;
  justify-content: flex-start;
  flex-direction: column;
  border-top-left-radius: 13px;
  border-bottom-left-radius: 13px;
  max-height: 330px;
  overflow-y: scroll;
  transition: all 0.2s ease-in;
}

.indexing:hover {
  width: 300px;
  box-shadow: 0 0 15px #ededed;
  border-radius: 8px;
  background-color: white;
}

.studentSidebarQuestion:hover {
  /* color: #1a1b1e; */
  color: #3a3c43;
  opacity: 1;
  font-weight: 600;
}

.indexing:hover .studentSidebarQuestion > p {
  display: block;
}

.studentSidebarContainer {
  display: flex;
  align-items: center;
}

